import React, {Component} from 'react'

import PDFViewer from '../utils/PDFViewer'
import TXTViewer from '../utils/TXTViewer'
import Loader from './Loader'

const PDF = 'application/pdf'
const TEXT = 'text/plain'
const IMAGE = 'image/'

export default class DocumentView extends Component{

    constructor(props, context) {
        super(props, context)

        this.state = {
            url: '',
            loading: true
        }

        this.renderMethod = this.renderSimple
    }

    componentWillMount() {
        console.log('View mount')
        //get file link
        cozy.client.files.getDownloadLinkById(this.props.file.id)
        .then(url => cozy.client.fullpath(url))
        .then(url => this.setState({url, loading: false}))
        
        const types = {
            [PDF]: this.renderPDF,
            [TEXT]: this.renderText,
            [IMAGE]: this.renderImage
        }

        for(const mime of Object.keys(types)) {
            if(this.props.file.mime.indexOf(mime) !== -1){
                this.renderMethod = types[mime]
                break
            }
        }
    }

    renderSimple() {
        return (
            <p>URL du fichier : <a href={this.state.url}>{this.state.url}</a></p>
        )
    }

    renderText() {
        return (
            <TXTViewer url={this.state.url}/>
        )
    }

    renderImage() {
        return (
            <div>
                <a href={this.state.url}>
                    <img style={{maxWidth: '100%'}} src={this.state.url} alt={this.props.file.name}/>
                </a>
            </div>
        )
    }

    renderPDF() {
        return (
            <PDFViewer url={this.state.url}/>
        )
    }   
    
    render() {
        if(this.state.loading)
            return (<Loader display={true}/>)
        else
            return this.renderMethod()
    }


}


