import React, {Component} from 'react'
import classNames from 'classnames'

import button from '../../styles/buttons.scss'

import Loader from './Loader'
import Page from './Page'
import DataDetails from './DataDetails'

import { getInfosFromSlug, getFields } from '../../scripts/dataFields'

export default class Data extends Component {

    constructor(props, context) {
        super(props, context)

        this.state = {
            edition: false
        }

        console.log('Data', props)
    }

    componentWillMount () {
        this.infos = getInfosFromSlug(this.props.match.params.slug)
        this.id = this.props.match.params.id

        this.props.onLoadData(this.infos.doctype, this.id)
    }

    save() {
        this.setState({
            edition: false
        })

        this.props.dataSave(this.infos.doctype, this.props.current)
    }

    edit() {
        this.setState({
            edition: true
        })
    }

    remove() {
        this.props.dataRemove(this.infos.doctype, [this.props.current])
        .then(e => this.props.history.goBack())
    }

    renderData(doctype) {
        const fields = getFields(this.infos.doctype)

        const { current } = this.props

        return (
            <DataDetails>
                {fields.map((group, keygroup) => (
                    <DataDetails.Group title={group.name} key={keygroup}>
                        {_.flatMap(group.group, (item, key) => {
                            if(item.type === 'contract') {
                                if(!current[item.name]) current[item.name] = {};
                                return [
                                    (
                                        <DataDetails.Item
                                        key={key+0.1}
                                        title={"Société"}
                                        value={current[item.name].company}
                                        type={"text"}
                                        updatable={this.state.edition}
                                        onChange={(e) => current[item.name].company = e}
                                        />
                                    ),
                                    (
                                        <DataDetails.Item
                                        key={key+0.2}
                                        title={"Adresse"}
                                        value={current[item.name].address}
                                        type={"text"}
                                        updatable={this.state.edition}
                                        onChange={(e) => current[item.name].address = e}
                                        />
                                    ),
                                    (
                                        <DataDetails.Item
                                        key={key+0.3}
                                        title={"Email"}
                                        value={current[item.name].email}
                                        type={"email"}
                                        updatable={this.state.edition}
                                        onChange={(e) => current[item.name].email = e}
                                        />
                                    ),
                                    (
                                        <DataDetails.Item
                                        key={key+0.4}
                                        title={"Téléphone"}
                                        value={current[item.name].telephone}
                                        type={"telephone"}
                                        updatable={this.state.edition}
                                        onChange={(e) => current[item.name].telephone = e}
                                        />
                                    )
                                ];
                            } else if(item.type === 'address') {
                                if(!current[item.name]) current[item.name] = {};
                                return [
                                    (
                                        <DataDetails.Item
                                        key={key+0.1}
                                        title={"Adresse"}
                                        value={current[item.name].way}
                                        type={"text"}
                                        updatable={this.state.edition}
                                        onChange={(e) => current[item.name].way = e}
                                        />
                                    ),
                                    (
                                        <DataDetails.Item
                                        key={key+0.2}
                                        title={"Code postal"}
                                        value={current[item.name].postalCode}
                                        type={"text"}
                                        updatable={this.state.edition}
                                        onChange={(e) => current[item.name].postalCode = e}
                                        />
                                    ),
                                    (
                                        <DataDetails.Item
                                        key={key+0.3}
                                        title={"Ville"}
                                        value={current[item.name].city}
                                        type={"text"}
                                        updatable={this.state.edition}
                                        onChange={(e) => current[item.name].city = e}
                                        />
                                    )
                                ];
                            } else if(item.type === 'multi') {
                                if(!current[item.name]) current[item.name] = {};
                                return [
                                    (
                                        <DataDetails.Item
                                        key={key}
                                        title={item.display+" Multi"}
                                        value={current[item.name]}
                                        type={"text"}
                                        updatable={this.state.edition}
                                        onChange={(e) => current[item.name] = e}
                                        />
                                    )
                                ]
                            } else {
                                return (
                                    <DataDetails.Item
                                    key={key}
                                    title={item.display}
                                    value={current[item.name]}
                                    type={item.type}
                                    updatable={this.state.edition}
                                    values={item.values}
                                    onChange={(e) => current[item.name] = e}
                                    />
                                );
                            }
                        })}
                    </DataDetails.Group>
                ))}
            </DataDetails>
        )
    }

    render() {
        const loading = !this.props.current

        const actions = []

        const remove = (<button onClick={() => this.remove()} className={classNames(button.button, button.assertive)} title="Supprimer"><i className="ion-ios-trash-outline"></i><span className={button.text}>SUPPRIMER</span></button>)
        actions.push(remove)

        if(this.state.edition) {
            const save = (<button onClick={() => this.save()} className={classNames(button.button, button.stable)} title="Sauvegarder"><i className="fa fa-floppy-o"></i><span className={button.text}>SAUVEGARDER</span></button>)
            actions.unshift(save)
        } else {
            const edit = (<button onClick={() => this.edit()} className={classNames(button.button, button.stable)} title="Modifier"><i className="fa fa-edit"></i><span className={button.text}>MODIFIER</span></button>)
            actions.unshift(edit)            	
        }

        let title = this.infos.name
        if(this.props.current && this.props.current.name) title += '/'+this.props.current.name

        return (
            <Page title={title} subtitle={''} 
                actions={actions}
                onBackPress={() => this.props.history.goBack()}>

                <Loader display={loading}/>

                {!loading && this.renderData.bind(this)()}
            </Page>
        )
    }
}


