import React, {Component} from 'react'
import classNames from 'classnames'
// import "./Preferences.scss"
import button from '../../styles/buttons.scss'
import style from '../../styles/Preferences.sass'
import Page from './Page'

import Exporter from '../../scripts/XMLDataFile.js'

import { markAsShown } from '../../scripts/onboarding'

import { forceFileDownload } from '../../tools'

import { DOCTYPE_META } from '../../constants'

export const Preferences = (props) => {

    const resetOnboarding = () => {
        markAsShown(false)
        .then(() => {
            props.history.push('/')
        })
    }

    return (
        <Page title="Préférences">
            <article>
                <h2 className={style.title}>Importer votre Carnet Numérique du Logement (CNL)</h2>
                <div className={style.article}>
                    <p>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Integer molestie, ligula quis
                        varius tristique, ex orci congue
                        metus, eget accumsan enim diam vitae eros. Nullam ut nulla ac justo varius egestas. In
                        id
                        dapibus sapien. Nunc vel
                        fringilla eros, ac tincidunt felis.
                    </p>
                    <button className={classNames(button.button, button.default)} disabled>
                        <i className="ion-ios-upload-outline"></i> IMPORTER CNL
                    </button>
                </div>
            </article>

            <article>
                <h2 className={style.title}>Exporter votre HOMEBOOK</h2>
                <div className={style.article}>
                    <p>
                        Proin nisl libero, aliquet quis tristique id, scelerisque eget dui. Duis ultricies,
                        tellus
                        ac posuere blandit, sapien elit
                        dapibus ligula, ut mollis tortor dui in massa. Orci varius natoque penatibus et magnis
                        dis
                        parturient montes,
                        nascetur ridiculus mus. Nam et orci odio.
                    </p>

                    <button className={classNames(button.button, button.default)} onClick={e => exporterHomebook()}>
                        <i className="ion-ios-upload-outline"></i> EXPORTER HOMEBOOK
                    </button>
                    
                </div>
            </article>

            <article>
                <h2 className={style.title}>Reset Onboarding</h2>
                <div className={style.article}>
                    <p>
                        Proin nisl libero, aliquet quis tristique id, scelerisque eget dui. Duis ultricies,
                        tellus
                        ac posuere blandit, sapien elit
                        dapibus ligula, ut mollis tortor dui in massa. Orci varius natoque penatibus et magnis
                        dis
                        parturient montes,
                        nascetur ridiculus mus. Nam et orci odio.
                    </p>

                    <button className={classNames(button.button, button.assertive)} onClick={() => resetOnboarding()}><i className="ion-ios-refresh-outline"></i> RESET ONBOARDING</button>
                </div>
            </article>
        </Page>
    )
}

const exporterHomebook = () => {
    Exporter()
    .then(link => forceFileDownload(link, 'homebook.zip'))
}

export default Preferences


