import React, {Component} from 'react'
import style from '../../styles/DataDetails.sass'
import _ from 'lodash'
import moment from 'moment'
import DatePicker from 'react-datepicker'

export const DataDetailsGroup = (props) => {
    return (
        <div>
            <h2>{props.title || (<span>{' '}</span>)}</h2>
            <table className={style.group}>
                <tbody>{props.children}</tbody>
            </table>
        </div>
    )
}

export class DataDetailsItem extends Component {
    constructor(props, context) {
        super(props, context)

        this.state = {
            value: props.value
        }
    }

    _onChangePhone(val) {
        // check if is a phone number
        let checkPhone = /0\d?([. -]?\d\d){0,4}\d?/

        if(checkPhone.test(val))
            this._onChange(val)
    }

    _onChange(value) {

        this.setState({
            value: value
        })

        if(this.props.type === "date") value = moment(value).format('DD/MM/YYYY')

        if (_.isFunction(this.props.onChange))
            this.props.onChange(value)
    }

    componentWillReceiveProps(props, context) {
        this.setState({
            value: props.value
        })
    }

    renderValue() {

        var value = this.state.value
        var type = this.props.type || 'text'

        // console.log("Value", value, type, this.props.title)

        if(! this.props.updatable) {
            switch (type) {
                case 'boolean':
                    value = value === 1 ? 'Oui' : 'Non'
                    break
            }
            if(_.isObject(value)) value = ""
            return value

        } else {
            switch (type) {
                case 'boolean':
                    return (
                        <select value={value} onChange={(e) => this._onChange(e.target.value) }>
                            <option value={0}>Non</option>
                            <option value={1}>Oui</option>
                        </select>
                    )

                case 'list':
                    if(!value) {
                        if (_.isFunction(this.props.onChange))
                           this.props.onChange(this.props.values && this.props.values[0])
                    }
                    return (
                        <select value={value} onChange={(e) => this._onChange(e.target.value) }>
                            {this.props.values.map((item, key) => (
                                <option key={key} value={item}>{item}</option>
                            ))}
                        </select>
                    )

                case 'date':
                    if(!value){
                         value = moment()
                         if (_.isFunction(this.props.onChange))
                            this.props.onChange(value.format('DD/MM/YYYY'))
                    } else {
                        value = moment(value, 'DD/MM/YYYY')
                    }
                    // console.log("Render date picker", value)
                    return (
                        <DatePicker
                            dateFormat="DD/MM/YYYY"
                            locale="fr-fr"
                            isClearable={false}
                            selected={value}
                            onChange={(e) => this._onChange(e)} />
                    )

                case 'email':
                    return (
                        <input type="email" value={value} onChange={(e) => this._onChange(e.target.value)} placeholder={this.props.title}/>
                    )

                case 'telephone':
                    return (
                        <input type="text" value={value} onChange={(e) => this._onChangePhone(e.target.value)} placeholder={this.props.title}/>
                    )

                case 'textarea':
                    return (
                        <textarea value={value} onChange={(e) => this._onChange(e.target.value) } placeholder={this.props.title}></textarea>
                    )

                default:
                    return (<input type={type} value={value} placeholder={this.props.title} onChange={(e) => this._onChange(e.target.value) }/>)

            }
        }
    }

    render() {
        return (
            <tr className={style.item}>
                <th>
                    <div>{this.props.title}</div>
                </th>
                <td>
                    {this.renderValue()}
                </td>
            </tr>
        )
    }
}

export const DataDetails = (props) => {
    return (
        <form className={style.main} onSubmit={() => props.onSubmit && props.onSubmit()}>
            {props.children}
        </form>
    )
}

DataDetails.Item = DataDetailsItem
DataDetails.Group = DataDetailsGroup

export default DataDetails


